}
.header__logo[href]:hover,
.header__logo[href]:focus {
  opacity: 0.6;
}
.header__logo[href]:active {
  opacity: 0.3;
}
.header__toggle {
  display: block;
  position: absolute;
  width: 40px;
  height: 40px;
  top: 20px;
  right: 20px;
  border: none;
  background-color: transparent;
  padding: 0 10px;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}
.header__toggle--nojs {
  display: none;
}
.header__toggle-content {
  position: relative;
  display: block;
  width: 20px;
  height: 2px;
  background-color: #000000;
}
.header__toggle-content::before {
  content: "";
  position: absolute;
  top: 6px;
  display: block;
  width: 20px;
  height: 2px;
  background-color: #000000;
  transition: transform 0.2s linear, top 0.2s linear 0.2s;
}
.header__toggle-content::after {
  content: "";
  position: absolute;
  top: -6px;
  display: block;
  width: 20px;
  height: 2px;
  background-color: #000000;
  transition: transform 0.2s linear, top 0.2s linear 0.2s;
}
.header__toggle--active .header__toggle-content {
  background-color: transparent;
  transition: background-color 0.2s;
}
.header__toggle--active .header__toggle-content::before {
  transform: rotate(135deg);
  top: 50%;
  transition: top 0.2s linear, transform 0.3s linear 0.2s;
}
.header__toggle--active .header__toggle-content::after {
  transform: rotate(-135deg);
  top: 50%;
  transition: top 0.2s linear, transform 0.3s linear 0.2s;
}
