.contacts {
  margin-bottom: 38px;
  padding-top: 73px;
  background-image: url("../img/bg-zigzag-line.svg");
  background-repeat: repeat-x;
  background-position: 56px 33px;
  background-size: 319px;


  @media (min-width: @tablet) {
    padding-top: 127px;
    margin-bottom: 56px;
    background-position: -20px 66px;
    background-size: 770px;
    position: relative;
  }

  @media (min-width: @desktop) {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 1050px;
    margin: 0 auto;
    padding-top: 186px;
    margin-bottom: 104px;
    background-position: -26px 34px;
    background-size: 1050px;
  }
}

.contacts__title {
  margin: 0;
  padding: 0 28px;

  .contacts-title();

  @media (min-width: @tablet) {
    margin: 0 auto;
    box-sizing: border-box;
    padding: 0 7px 0 0;
    width: 88%;

    display: flex;
    justify-content: space-between;
    align-items: center;

    .tablet-title();
  }

  @media (min-width: @desktop) {
    width: 386px;
    margin-left: 63px;

    .desktop-title();
  }
}

.contacts__text {
  @media (min-width: @tablet) {
    order: 1;
    width: 550px;
  }

  @media (min-width: @desktop) {
    order: 2;
    width: 272px;
  }
}

.contacts__info {
  padding-left: 16px;

  @media (min-width: @tablet) {
    width: 87%;
    margin: 3px auto 14px;
    padding-left: 0;
  }

  @media (min-width: @desktop) {
    width: 335px;
    margin-left: 157px;
    margin-top: 6px;
    padding-left: 16px;
  }
}

.contacts__table {
  border-collapse: separate;
  border-spacing: 13px 26px;
  font-style: normal;
  line-height: 30px;

  .product-properties();

  @media (min-width: @tablet) {
    .review-cust-email();

    border-spacing: 0 15px;
    width: 100%;
  }

  @media (min-width: @desktop) {
    border-spacing: 0 14px;
  }

  .contacts__email {
    width: 74px;
    height: 30px;
  }

  .contacts__body {
    @media (min-width: @tablet) {
      display: flex;
    }

    @media (min-width: @desktop) {
      flex-wrap: wrap;
      width: 334px;
    }
  }

  .contacts__email-address {
    border-bottom: 1px solid @green;

    @media (max-width: @mobile-width-only) {
      width: 181px;
    }

    @media (min-width: @tablet) {
      width: 207px;
    }
  }

  .contacts__address {
    vertical-align: baseline;
    width: 74px;
  }

  .contacts__ph-address {
    width: 170px;
    height: 130px;
    vertical-align: baseline;
    padding-right: 4px;

    @media (min-width: @tablet) {
      height: 101px;
      width: 250px;
    }
  }
}

.contacts__email-link {
  text-decoration: none;
  color: @grey--text;
}

.contacts__row {
  @media (min-width: @tablet) {
    width: 50%;
    display: block;
  }

  @media (min-width: @desktop) {
    width: 334px;
  }
}

.contacts__map {
  @media (min-width: @desktop) {
    position: absolute;
    top: 127px;
    right: 0;
    z-index: 3;
  }
}

.contacts__map-logo {
  margin: 0 auto;
  width: 100%;
  height: 457px;
  background-color: @grey--light;

  @media (min-width: @desktop) {
    width: 526px;
  }
}

.contacts__btn {
  margin-top: 37px;
  font-size: 18px;

  @media (min-width: @tablet) {
    margin-top: 51px;
    width: 230px;
    font-size: 17px;
  }

  @media (min-width: @desktop) {
    margin-left: 175px;
    margin-top: 25px;
  }
}

.contacts__email-link:hover,
.contacts__email-link:focus {
  color: @green--dark-hover;
}

.contacts__email-link:active {
  color: @green--dark-hover;
}

.contacts__real-map {
  width: 100%;
  height: 457px;
  padding: 0;
  margin: 0;
  position: absolute;
  z-index: 5;

  @media (min-width: @desktop) {
    width: 526px;
  }
}

.contacts__icon {
  display: none;

  @media (min-width: @tablet) {
    display: block;
    order: 2;
    align-self: flex-start;
  }

  @media (min-width: @desktop) {
    order: 1;
    margin-left: 0;
  }
}
